@import "colors";

$page-width: 45em;
$base-font-family: "Source Sans Pro", Helvetica, Arial, sans-serif;

html, body {
  background-color: white; }

body {
  margin: 0 auto;
  width: 95%;
  max-width: $page-width;
  color: $font-color;
  font-family: $base-font-family; }

h1 {
  color: $secondary-color; }

a:link {
  color: $link-color; }

a:visited {
  color: $visited-color; }

.center {
  text-align: center;
  margin: 0 auto; }

.footer {
  margin-top: 1em;

  ul {
    padding: 0;

    li {
      color: black;
      display: inline-block;
      margin: 1rem .5rem;
      width: 2rem;
      height: 2rem;
      position: relative;
      box-shadow: 0 0 2px rgba(0, 0, 0, 0);
      transition-duration: 0.3s;
      transition-property: transform;

      &:hover {
        transform: scale(1.1); } } } }


